@use '../../styles/variables' as *;

/* ══════════════════════════════════════
   GLOBAL KEYFRAMES
   ══════════════════════════════════════ */
@keyframes cs-pulse-diamond {
  0%, 100% { opacity: 0.6; transform: scale(1); }
  50% { opacity: 1; transform: scale(1.3); }
}

@keyframes cs-twinkle {
  0%, 100% { opacity: 1; filter: brightness(1); }
  50% { opacity: 0.7; filter: brightness(1.4); }
}

@keyframes cs-twinkle-dark {
  0%, 100% { opacity: 1; filter: brightness(1); }
  50% { opacity: 0.8; filter: brightness(0.65); }
}

@keyframes cs-line-shimmer {
  0% { background-position: -200% center; }
  100% { background-position: 200% center; }
}

@keyframes cs-bar-pulse {
  0%, 100% { box-shadow: inset 0 1px 0 color-mix(in srgb, var(--ci-light) 30%, transparent); }
  50% { box-shadow: inset 0 1px 0 color-mix(in srgb, var(--ci-light) 30%, transparent), 0 0 8px color-mix(in srgb, var(--ci-primary) 20%, transparent); }
}

@keyframes cs-bar-pulse-accent {
  0%, 100% { box-shadow: inset 0 1px 0 color-mix(in srgb, var(--ci-light) 30%, transparent); }
  50% { box-shadow: inset 0 1px 0 color-mix(in srgb, var(--ci-light) 30%, transparent), 0 0 8px color-mix(in srgb, var(--ci-accent) 20%, transparent); }
}

@keyframes cs-portrait-border {
  0%, 100% { box-shadow: 0 0 0 2px color-mix(in srgb, var(--ci-primary) 30%, transparent), 0 8px 28px color-mix(in srgb, var(--ci-dark) 15%, transparent); }
  50% { box-shadow: 0 0 0 2px color-mix(in srgb, var(--ci-primary) 45%, transparent), 0 8px 28px color-mix(in srgb, var(--ci-dark) 15%, transparent), 0 0 20px color-mix(in srgb, var(--ci-primary) 8%, transparent); }
}

@keyframes cs-slot-float {
  0%, 100% { transform: translateY(0); }
  50% { transform: translateY(-2px); }
}

@keyframes cs-wish-glow {
  0%, 100% { box-shadow: none; }
  50% { box-shadow: 0 0 10px color-mix(in srgb, var(--ci-accent) 12%, transparent); }
}

/* ══════════════════════════════════════
   CHARACTER SHEET
   ══════════════════════════════════════ */

.cs {
  position: relative;
  display: flex;
  gap: 0;
  height: 100vh;
  padding: 0.5rem;
  box-sizing: border-box;
  overflow: hidden;

  &--loading {
    align-items: center;
    justify-content: center;
  }

  @media (min-width: $bp-expanded-wide) {
    padding: 0.8rem;
  }

  @media (max-width: $bp-medium) {
    flex-direction: column;
    height: auto;
    overflow: visible;
    padding: 0.5rem;
  }

  @media (max-width: $bp-compact) {
    padding: 0.5rem 0.35rem;
  }

  @media (max-width: $bp-mobile) {
    padding: 0.5rem 0.2rem;
  }
}

/* ════════════════════════════════════
   LEFT — Light Character Card
   ════════════════════════════════════ */
.cs__left {
  flex-shrink: 0;
  width: 320px;
  display: flex;
  flex-direction: column;
  gap: 1rem;
  padding: 0.8rem;
  border-radius: 20px 0 0 20px;
  position: relative;
  background:
    linear-gradient(175deg,
      color-mix(in srgb, var(--ci-left-grad1) 14%, var(--ci-surface)) 0%,
      var(--ci-surface) 40%,
      color-mix(in srgb, var(--ci-left-grad2) 8%, var(--ci-surface)) 100%);
  border: 1px solid var(--ci-border);
  border-right: none;
  box-shadow:
    inset 0 1px 0 color-mix(in srgb, var(--ci-light) 50%, transparent);
  z-index: 1;

  @media (min-width: $bp-expanded-wide) {
    width: 360px;
  }

  @media (max-width: $bp-medium) {
    width: 100%;
    overflow: visible;
    border-radius: 20px 20px 0 0;
    border-right: none;
    border-bottom: none;
    border: 1px solid var(--ci-border);
    border-bottom: none;
  }

  @media (max-width: $bp-compact) {
    padding: 0.6rem;
    gap: 0.6rem;
    border-radius: 16px 16px 0 0;
  }

  @media (max-width: $bp-mobile) {
    padding: 0.6rem 0.4rem;
    border-radius: 12px 12px 0 0;
  }
}

/* ── Nickname header ── */
.cs__nick-header {
  text-align: center;
  flex-shrink: 0;
}

.cs__nick {
  font-family: 'Playfair Display', serif;
  font-size: 2.2rem;
  font-weight: 700;
  color: var(--ci-fg);
  margin: 0;
  line-height: 1.2;
  letter-spacing: 0.08em;
  text-transform: uppercase;
  text-shadow: 0 1px 2px color-mix(in srgb, var(--ci-primary) 8%, transparent);

  @media (min-width: $bp-expanded-wide) {
    font-size: 2.4rem;
  }

  @media (max-width: $bp-medium) {
    font-size: 1.9rem;
  }

  @media (max-width: $bp-compact) {
    font-size: 1.6rem;
  }

  @media (max-width: $bp-mobile) {
    font-size: 1.35rem;
    letter-spacing: 0.05em;
  }
}

.cs__nick-sub {
  font-family: 'Noto Sans Thai', 'Inter', sans-serif;
  font-size: 0.85rem;
  color: var(--ci-muted);
  margin: 0.25rem 0 0;
  letter-spacing: 0.03em;

  @media (max-width: $bp-medium) {
    font-size: 0.75rem;
  }

  @media (max-width: $bp-compact) {
    font-size: 0.7rem;
  }

  @media (max-width: $bp-mobile) {
    font-size: 0.62rem;
  }
}

/* ── Portrait ── */
.cs__portrait {
  position: relative;
  flex: 1;
  min-height: 0;
  border-radius: 14px;
  overflow: hidden;
  box-shadow:
    0 0 0 2px color-mix(in srgb, var(--ci-primary) 30%, transparent),
    0 8px 28px color-mix(in srgb, var(--ci-dark) 15%, transparent);
  animation: cs-portrait-border 4s ease-in-out infinite;

  @media (max-width: $bp-medium) {
    aspect-ratio: 3 / 4;
    flex: none;
    max-height: 55vh;
  }

  @media (max-width: $bp-compact) {
    max-height: 60vh;
  }

  @media (max-width: $bp-mobile) {
    max-height: 55vh;
  }
}

.cs__portrait-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center center;
  display: block;
}

.cs__portrait-ph {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(160deg, var(--ci-primary), var(--ci-dark));
  font-family: 'Playfair Display', serif;
  font-size: 5rem;
  font-weight: 700;
  color: var(--ci-light);
}

.cs__twitter {
  position: absolute;
  top: 0.5rem;
  right: 0.5rem;
  z-index: 2;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background: color-mix(in srgb, var(--ci-dark) 55%, transparent);
  color: var(--ci-overlay-text, #fff);
  text-decoration: none;
  backdrop-filter: blur(6px);
  transition: background 0.2s, transform 0.2s;

  svg {
    width: 14px;
    height: 14px;
  }

  &:hover {
    background: color-mix(in srgb, var(--ci-dark) 75%, transparent);
  }

  @media (max-width: $bp-compact) {
    width: 26px;
    height: 26px;

    svg {
      width: 12px;
      height: 12px;
    }
  }
}

.cs__overlay {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 3rem 0.8rem 1.5rem;
  background: linear-gradient(to top, color-mix(in srgb, var(--ci-overlay, var(--ci-dark)) 90%, transparent) 0%, transparent 100%);
  text-align: center;
  display: flex;
  flex-direction: column;
  gap: 0.4rem;
}

.cs__name {
  font-family: 'Playfair Display', serif;
  font-size: 1.5rem;
  font-weight: 700;
  color: var(--ci-overlay-text, #fff);
  margin: 0;
  line-height: 1.1;
  text-shadow: 0 2px 12px rgba(0, 0, 0, 0.5);
}

.cs__name-th {
  font-family: 'Noto Sans Thai', 'Inter', sans-serif;
  font-size: 0.72rem;
  color: color-mix(in srgb, var(--ci-overlay-text, #fff) 80%, transparent);
  margin: 0.5rem 0 -0.3rem;
}

.cs__deity {
  font-family: 'Inter', 'Noto Sans Thai', sans-serif;
  font-size: 0.6rem;
  font-weight: 500;
  color: color-mix(in srgb, var(--ci-overlay-text, #fff) 75%, transparent);
  margin: 0.3rem 0 0;
  letter-spacing: 0.04em;
}

/* ── HP / SPD bars ── */
.cs__bars {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  flex-shrink: 0;
  margin: 0.5rem 0 0.6rem;

  @media (max-width: $bp-compact) {
    gap: 0.6rem;
    margin: 0.4rem 0 0.5rem;
  }
}

.vbar {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.vbar__label {
  font-family: 'Inter', 'Noto Sans Thai', sans-serif;
  font-size: 0.7rem;
  font-weight: 800;
  color: var(--ci-fg);
  width: 28px;
  text-align: right;
  flex-shrink: 0;
  letter-spacing: 0.05em;
}

.vbar__track {
  flex: 1;
  height: 22px;
  border-radius: 6px;
  overflow: hidden;
  background: color-mix(in srgb, var(--ci-bg-alt) 60%, var(--ci-surface));
  border: 1px solid var(--ci-border);
}

.vbar__fill {
  height: 100%;
  border-radius: 5px;
  background: linear-gradient(90deg, var(--ci-primary), color-mix(in srgb, var(--ci-primary-hover) 55%, var(--ci-highlight)));
  transition: width 0.6s ease;
  box-shadow: inset 0 1px 0 color-mix(in srgb, var(--ci-light) 30%, transparent);
  animation: cs-bar-pulse 3s ease-in-out infinite;

  &--accent {
    background: linear-gradient(90deg, var(--ci-accent), color-mix(in srgb, var(--ci-accent-soft) 65%, var(--ci-highlight)));
    animation-name: cs-bar-pulse-accent;
  }
}

.vbar__num {
  font-family: 'DM Mono', monospace;
  font-size: 1rem;
  font-weight: 500;
  color: var(--ci-fg);
  width: 22px;
  text-align: center;

  @media (max-width: $bp-compact) {
    font-size: 0.8rem;
  }
}

/* ════════════════════════════════════
   RIGHT — Light Journal Panel
   ════════════════════════════════════ */
.cs__right {
  flex: 1;
  min-width: 0;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  border-radius: 0 20px 20px 0;
  background:
    radial-gradient(ellipse at 30% 20%, color-mix(in srgb, var(--ci-right-grad1) 6%, var(--ci-surface)) 0%, transparent 60%),
    radial-gradient(ellipse at 80% 80%, color-mix(in srgb, var(--ci-right-grad2) 8%, var(--ci-surface)) 0%, transparent 50%),
    var(--ci-surface);
  border: 1px solid var(--ci-border);
  border-left: none;
  box-shadow: inset 0 1px 0 color-mix(in srgb, var(--ci-highlight) 30%, transparent);

  @media (max-width: $bp-medium) {
    border-left: 1px solid var(--ci-border);
    border-radius: 0 0 20px 20px;
    overflow: visible;
  }

  @media (max-width: $bp-compact) {
    border-radius: 0 0 16px 16px;
  }

  @media (max-width: $bp-mobile) {
    border-radius: 0 0 12px 12px;
  }
}

/* ══════════════════════════════════
   CURRENCY BANNER
   ══════════════════════════════════ */
@keyframes shimmer {
  0% {
    background-position: -200% center;
  }

  100% {
    background-position: 200% center;
  }
}

@keyframes float-star {

  0%,
  100% {
    transform: translateY(0) rotate(0deg);
    opacity: 0.5;
  }

  50% {
    transform: translateY(-3px) rotate(15deg);
    opacity: 1;
  }
}

.cs__currency {
  flex-shrink: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.3rem;
  padding: 1rem;

  @media (max-width: $bp-medium) {
    padding: 1rem 0.5rem;
  }

  @media (max-width: $bp-compact) {
    padding: 0.7rem 0.4rem;
  }

  @media (max-width: $bp-mobile) {
    padding: 0.7rem 0.3rem;
  }
}

.cs__currency-line {
  width: 70%;
  height: 1px;
  background: linear-gradient(90deg,
      transparent,
      color-mix(in srgb, var(--ci-primary) 15%, transparent) 15%,
      color-mix(in srgb, var(--ci-primary) 50%, transparent) 35%,
      color-mix(in srgb, var(--ci-primary) 80%, transparent) 50%,
      color-mix(in srgb, var(--ci-primary) 50%, transparent) 65%,
      color-mix(in srgb, var(--ci-primary) 15%, transparent) 85%,
      transparent);
  background-size: 200% 100%;
  animation: cs-line-shimmer 8s ease-in-out infinite;
}

.cs__currency-inner {
  display: flex;
  align-items: center;
  gap: 1.5rem;

  @media (max-width: $bp-medium) {
    gap: 0.8rem;
  }

  @media (max-width: $bp-compact) {
    gap: 0.5rem;
  }
}

.cs__currency-star {
  font-size: 0.8rem;
  color: var(--ci-primary);
  margin-right: -15px;
  animation: float-star 2.5s ease-in-out infinite;

  &:last-child {
    animation-delay: 1.25s;
    margin-left: -15px;
    margin-right: 0;
  }

  @media (max-width: $bp-medium) {
    display: none;
  }
}

.cs__currency-amount {
  display: flex;
  align-items: center;
  gap: 1.2rem;

  @media (max-width: $bp-medium) {
    gap: 0.6rem;
  }
}

.cs__currency-icon {
  width: 1.3rem;
  height: 1.3rem;
  color: color-mix(in srgb, var(--ci-primary) 85%, var(--ci-dark));
  align-self: center;
  flex-shrink: 0;
  animation: cs-twinkle-dark 3s ease-in-out infinite;

  @media (max-width: $bp-mobile) {
    width: 1rem;
    height: 1rem;
  }
}

.cs__currency-num {
  font-family: 'DM Mono', monospace;
  font-size: 2.2rem;
  font-weight: 500;
  line-height: 1;
  background: linear-gradient(90deg,
      var(--ci-primary-dark) 0%,
      color-mix(in srgb, var(--ci-primary-dark) 75%, var(--ci-primary)) 25%,
      var(--ci-primary-dark) 50%,
      color-mix(in srgb, var(--ci-primary-dark) 75%, var(--ci-primary)) 75%,
      var(--ci-primary-dark) 100%);
  background-size: 200% 100%;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  animation: shimmer 6s ease-in-out infinite;

  @media (min-width: $bp-expanded-wide) {
    font-size: 2.5rem;
  }

  @media (max-width: $bp-medium) {
    font-size: 1.5rem;
    margin: 0.3rem 0;
  }

  @media (max-width: $bp-compact) {
    font-size: 1.4rem;
  }

  @media (max-width: $bp-mobile) {
    font-size: 1rem;
    margin: 0.5rem 0;
  }
}

.cs__currency-label {
  font-family: 'Playfair Display', serif;
  font-size: 0.85rem;
  font-weight: 700;
  color: var(--ci-muted);
  text-transform: uppercase;
  letter-spacing: 0.15em;

  @media (max-width: $bp-medium) {
    font-size: 0.7rem;
    letter-spacing: 0.1em;
  }

  @media (max-width: $bp-compact) {
    font-size: 0.65rem;
  }

  @media (max-width: $bp-mobile) {
    font-size: 0.6rem;
  }
}

.cs__currency-sep {
  font-size: 1.2rem;
  color: color-mix(in srgb, var(--ci-primary) 30%, transparent);
  align-self: center;

  @media (max-width: $bp-compact) {
    display: none;
  }
}

/* ══════════════════════════════════
   STAT ORBS — 3×2 grid
   ══════════════════════════════════ */
@keyframes orb-glow {

  0%,
  100% {
    filter: drop-shadow(0 0 3px color-mix(in srgb, var(--so-color) 25%, transparent));
  }

  50% {
    filter: drop-shadow(0 0 8px color-mix(in srgb, var(--so-color) 45%, transparent));
  }
}

.cs__stats {
  flex-shrink: 0;
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  gap: 0.5rem;
  padding: 0.7rem 0.8rem 1rem;

  @media (max-width: $bp-mobile) {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 0.4rem;
    padding: 0.3rem 0.5rem 0.8rem;
    row-gap: 1.25rem;

    .so { flex: 0 0 calc(25% - 0.3rem); }
    .so--accent { flex: 0 0 calc(33.33% - 0.27rem); }
  }
}

.so {
  --so-color: var(--ci-primary);
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.5rem;

  &--accent {
    --so-color: var(--ci-accent);
  }
}

.so__orb {
  position: relative;
  width: 64px;
  height: 64px;
  border-radius: 50%;
  background:
    radial-gradient(circle at 38% 32%,
      color-mix(in srgb, var(--so-color) 14%, var(--ci-bg-alt)),
      color-mix(in srgb, var(--so-color) 4%, var(--ci-bg-alt)) 65%,
      color-mix(in srgb, var(--so-color) 10%, var(--ci-bg-alt)));
  box-shadow:
    0 0 0 1px color-mix(in srgb, var(--so-color) 12%, transparent),
    0 2px 8px color-mix(in srgb, var(--ci-shadow) 20%, transparent);
  transition: transform 0.25s ease, box-shadow 0.25s ease;
  cursor: default;

  &:hover {
    box-shadow:
      0 0 0 1px color-mix(in srgb, var(--so-color) 15%, transparent),
      0 0 20px color-mix(in srgb, var(--so-color) 20%, transparent),
  }

  @media (min-width: $bp-expanded-wide) {
    width: 72px;
    height: 72px;
  }

  @media (max-width: $bp-medium) {
    width: 56px;
    height: 56px;
  }

  @media (max-width: $bp-compact) {
    width: 50px;
    height: 50px;
  }

  @media (max-width: $bp-mobile) {
    width: 48px;
    height: 48px;
  }
}

.so__svg {
  width: 100%;
  height: 100%;
  transform: rotate(-90deg);
  animation: orb-glow 3s ease-in-out infinite;
}

.so__track {
  fill: none;
  stroke: color-mix(in srgb, var(--so-color) 10%, transparent);
  stroke-width: 2.5;
}

.so__arc {
  fill: none;
  stroke: var(--so-color);
  stroke-width: 3;
  stroke-linecap: round;
  filter: drop-shadow(0 0 4px color-mix(in srgb, var(--so-color) 40%, transparent));
  transition: stroke-dashoffset 0.8s cubic-bezier(0.4, 0, 0.2, 1);
}

.so__inner {
  fill: none;
  stroke: color-mix(in srgb, var(--so-color) 6%, transparent);
  stroke-width: 1;
}

.so__val {
  position: absolute;
  inset: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: 'DM Mono', monospace;
  font-size: 1.5rem;
  font-weight: 600;
  line-height: 1;
  color: color-mix(in srgb, var(--so-color) 85%, var(--ci-dark));
  text-shadow: 0 0 14px color-mix(in srgb, var(--so-color) 30%, transparent);

  @media (min-width: $bp-expanded-wide) {
    font-size: 1.6rem;
  }

  @media (max-width: $bp-medium) {
    font-size: 1.3rem;
  }

  @media (max-width: $bp-compact) {
    font-size: 1.15rem;
  }

  @media (max-width: $bp-mobile) {
    font-size: 1.05rem;
  }
}

.so__lock-icon {
  position: absolute;
  inset: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 24px;
  height: 24px;
  margin: auto;
  color: color-mix(in srgb, var(--so-color) 85%, var(--ci-dark));
  filter: drop-shadow(0 0 6px color-mix(in srgb, var(--so-color) 25%, transparent));

  @media (max-width: $bp-mobile) {
    width: 20px;
    height: 20px;
  }
}

.so--locked {
  opacity: 0.45;
}

.so__label {
  font-family: 'Inter', sans-serif;
  font-size: 0.6rem;
  font-weight: 700;
  color: var(--ci-muted);
  text-transform: uppercase;
  letter-spacing: 0.12em;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  margin: auto 0;

  @media (max-width: $bp-compact) {
    font-size: 0.5rem;
  }

  @media (max-width: $bp-mobile) {
    font-size: 0.48rem;
  }
}

/* ══════════════════════════════════
   SCROLLABLE CONTENT
   ══════════════════════════════════ */
.cs__scroll {
  flex: 1;
  min-height: 0;
  overflow-y: auto;
  padding: 0.6rem 0.8rem;
  border-top: 1px solid var(--ci-border);

  scrollbar-width: thin;
  scrollbar-color: color-mix(in srgb, var(--ci-primary) 20%, transparent) transparent;

  &::-webkit-scrollbar {
    width: 5px;
  }

  &::-webkit-scrollbar-track {
    background: transparent;
  }

  &::-webkit-scrollbar-thumb {
    background: color-mix(in srgb, var(--ci-primary) 20%, transparent);
    border-radius: 3px;
  }

  @media (max-width: $bp-medium) {
    overflow: visible;
  }

  @media (max-width: $bp-mobile) {
    padding: 0.4rem 0.5rem;
  }
}

/* ══════════════════════════════════
   PRACTICE PROGRESS
   ══════════════════════════════════ */
.cs__practice {
  margin-bottom: 0.8rem;
  border-radius: 12px;
  background:
    linear-gradient(160deg,
      color-mix(in srgb, var(--ci-primary) 6%, var(--ci-bg-alt)),
      color-mix(in srgb, var(--ci-primary) 2%, var(--ci-bg-alt)) 70%);
  border: 1px solid color-mix(in srgb, var(--ci-primary) 12%, transparent);
  overflow: hidden;
  transition: box-shadow 0.25s ease;
}

.cs__practice-title {
  display: flex;
  align-items: center;
  gap: 0.45rem;
  padding: 0.55rem 0.7rem;
  border-bottom: 1px solid color-mix(in srgb, var(--ci-primary) 12%, transparent);
  background:
    linear-gradient(90deg,
      color-mix(in srgb, var(--ci-primary) 18%, var(--ci-bg-alt)),
      color-mix(in srgb, var(--ci-primary) 8%, var(--ci-bg-alt)));
  font-size: 0.8rem;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.12em;
  color: color-mix(in srgb, var(--ci-primary-hover) 90%, var(--ci-dark));
  margin: 0;
}

.cs__practice-diamond {
  font-size: 0.45rem;
  color: var(--ci-primary);
  animation: cs-pulse-diamond 2.5s ease-in-out infinite;
}

.cs__practice-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 0.15rem 0;
  padding: 0.5rem 0;

  @media (max-width: $bp-compact) {
    grid-template-columns: 1fr;
  }
}

.cs__practice-row {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 0.4rem;
  padding: 0.3rem 0.7rem;
  border-radius: 6px;

  @media (max-width: $bp-mobile) {
    padding: 0.25rem 0.5rem;
  }
}

.cs__practice-label {
  font-family: 'Inter', sans-serif;
  font-size: 0.65rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.06em;
  color: var(--ci-muted);

  @media (min-width: $bp-expanded-wide) {
    font-size: 0.7rem;
  }

  @media (max-width: $bp-compact) {
    font-size: 0.6rem;
  }

  @media (max-width: $bp-mobile) {
    font-size: 0.55rem;
  }
}

.cs__practice-stars {
  display: flex;
  gap: 0.15rem;
}

.cs__star {
  font-size: 1.4rem;
  line-height: 1;
  transition: transform 0.2s ease, color 0.2s ease;

  &--filled {
    color: color-mix(in srgb, var(--ci-primary) 85%, var(--ci-dark));
    text-shadow: 0 0 8px color-mix(in srgb, var(--ci-primary) 30%, transparent);
    animation: cs-twinkle 2s ease-in-out infinite;

    &:nth-child(2) { animation-delay: 0.3s; }
    &:nth-child(3) { animation-delay: 0.6s; }
    &:nth-child(4) { animation-delay: 0.9s; }
    &:nth-child(5) { animation-delay: 1.2s; }
  }

  &--empty {
    color: color-mix(in srgb, var(--ci-primary) 50%, transparent);
    text-shadow: none;
  }

  @media (min-width: $bp-expanded-wide) {
    font-size: 1.5rem;
  }

  @media (max-width: $bp-compact) {
    font-size: 1.25rem;
  }

  @media (max-width: $bp-mobile) {
    font-size: 1.1rem;
  }
}

/* ══════════════════════════════════
   DIVINE WISHES
   ══════════════════════════════════ */
.cs__wishes {
  margin-bottom: 0.8rem;
  border-radius: 12px;
  background:
    linear-gradient(160deg,
      color-mix(in srgb, var(--ci-accent-soft) 10%, var(--ci-bg-alt)),
      color-mix(in srgb, var(--ci-accent-soft) 4%, var(--ci-bg-alt)) 70%);
  border: 1px solid color-mix(in srgb, var(--ci-accent) 12%, transparent);
  overflow: hidden;
  transition: box-shadow 0.25s ease;
}

.cs__wishes-title {
  display: flex;
  align-items: center;
  gap: 0.45rem;
  padding: 0.55rem 0.7rem;
  border-bottom: 1px solid color-mix(in srgb, var(--ci-accent) 12%, transparent);
  background:
    linear-gradient(90deg,
      color-mix(in srgb, var(--ci-accent-soft) 22%, var(--ci-bg-alt)),
      color-mix(in srgb, var(--ci-accent-soft) 10%, var(--ci-bg-alt)));
  font-size: 0.8rem;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.12em;
  color: color-mix(in srgb, var(--ci-accent) 80%, var(--ci-dark));
  margin: 0;
}

.cs__wishes-diamond {
  font-size: 0.45rem;
  color: var(--ci-accent);
  animation: cs-pulse-diamond 2.5s ease-in-out infinite;
  animation-delay: 0.5s;
}

.cs__wishes-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 0.15rem;
  padding: 0.5rem;

  @media (max-width: $bp-compact) {
    grid-template-columns: repeat(3, 1fr);
  }

  @media (max-width: $bp-mobile) {
    grid-template-columns: repeat(2, 1fr);
  }
}

.cs__wish {
  display: flex;
  align-items: center;
  gap: 0.35rem;
  padding: 0.3rem 0.5rem;
  border-radius: 6px;
  transition: background 0.2s ease, opacity 0.2s ease, box-shadow 0.2s ease;
  opacity: 0.75;

  &:hover {
    opacity: 0.65;
    background: color-mix(in srgb, var(--ci-accent) 4%, transparent);
  }

  @media (max-width: $bp-mobile) {
    padding: 0.25rem 0.35rem;
  }
}

.cs__wish--active {
  opacity: 1;
  background: color-mix(in srgb, var(--ci-highlight) 25%, transparent);
  animation: cs-wish-glow 3s ease-in-out infinite;
}

.cs__wish-icon {
  flex-shrink: 0;
  width: 1.1rem;
  height: 1.1rem;
  color: var(--ci-muted);

  .cs__wish--active & {
    color: color-mix(in srgb, var(--ci-accent) 70%, var(--ci-dark));
  }

  svg {
    width: 100%;
    height: 100%;
  }
}

.cs__wish-deity {
  flex: 1;
  font-family: 'Inter', sans-serif;
  font-size: 0.6rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.04em;
  color: var(--ci-muted);

  .cs__wish--active & {
    color: color-mix(in srgb, var(--ci-accent) 80%, var(--ci-dark));
  }

  @media (max-width: $bp-compact) {
    font-size: 0.5rem;
  }

  @media (max-width: $bp-mobile) {
    font-size: 0.48rem;
  }
}

.cs__wish-count {
  font-family: 'DM Mono', monospace;
  font-size: 0.85rem;
  font-weight: 600;
  color: var(--ci-muted);

  .cs__wish--active & {
    color: color-mix(in srgb, var(--ci-accent) 85%, var(--ci-dark));
    text-shadow: 0 0 8px color-mix(in srgb, var(--ci-accent) 25%, transparent);
  }
}

/* ── Section headers ── */
.cs__section {
  margin-bottom: 1rem;
}

.cs__section-title {
  display: flex;
  align-items: center;
  gap: 0.6rem;
  margin: 0 0 0.6rem;
}

.cs__section-text {
  font-family: 'Playfair Display', serif;
  font-size: 0.85rem;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.15em;
  color: color-mix(in srgb, var(--ci-primary) 70%, var(--ci-dark));
  white-space: nowrap;

  @media (max-width: $bp-mobile) {
    font-size: 0.75rem;
  }
}

.cs__section-line {
  flex: 1;
  height: 1px;
  background:
    linear-gradient(90deg,
      color-mix(in srgb, var(--ci-primary) 15%, transparent),
      color-mix(in srgb, var(--ci-primary) 50%, transparent) 30%,
      color-mix(in srgb, var(--ci-primary) 15%, transparent) 60%,
      transparent);
  background-size: 200% 100%;
  animation: cs-line-shimmer 6s ease-in-out infinite;

  &:last-child {
    background:
      linear-gradient(270deg,
        color-mix(in srgb, var(--ci-primary) 15%, transparent),
        color-mix(in srgb, var(--ci-primary) 50%, transparent) 30%,
        color-mix(in srgb, var(--ci-primary) 15%, transparent) 60%,
        transparent);
    background-size: 200% 100%;
    animation: cs-line-shimmer 6s ease-in-out infinite;
    animation-delay: 3s;
  }
}

/* ══════════════════════════════════
   ARSENAL — side label + scrolling slots
   ══════════════════════════════════ */
.cs__arsenal-row {
  display: flex;
  gap: 0.5rem;
  margin-bottom: 0.6rem;

  @media (max-width: $bp-medium) {
    flex-direction: column;
  }
}

.cs__arsenal {
  flex: 1;
  min-width: 0;
  display: flex;
  position: relative;
  border-radius: 12px;
  background: color-mix(in srgb, var(--ci-primary) 3%, var(--ci-bg-alt));
  border: 1px solid var(--ci-border);
  overflow: hidden;
}

.cs__arsenal-label {
  flex-shrink: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 2rem;
  background: color-mix(in srgb, var(--ci-primary) 8%, var(--ci-bg-alt));
  border-right: 1px solid var(--ci-border);
}

.cs__arsenal-text {
  font-size: 0.7rem;
  font-weight: 700;
  text-transform: uppercase;
  color: color-mix(in srgb, var(--ci-primary) 70%, var(--ci-dark));
  writing-mode: vertical-rl;
  text-orientation: mixed;
  transform: rotate(180deg);
}

.cs__slots {
  display: flex;
  gap: 0.5rem;
  padding: 0.5rem;
  overflow-x: auto;
  scrollbar-width: thin;
  scrollbar-color: color-mix(in srgb, var(--ci-primary) 15%, transparent) transparent;

  &::-webkit-scrollbar {
    height: 4px;
  }

  &::-webkit-scrollbar-track {
    background: transparent;
  }

  &::-webkit-scrollbar-thumb {
    background: color-mix(in srgb, var(--ci-primary) 15%, transparent);
    border-radius: 2px;
  }
}

.cs__slots-more {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 2;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 0;
  overflow: hidden;
  border: none;
  background: linear-gradient(180deg,
      color-mix(in srgb, var(--ci-primary) 12%, var(--ci-bg)),
      color-mix(in srgb, var(--ci-primary) 6%, var(--ci-bg)));
  cursor: pointer;
  transition: width 0.25s ease;

  .cs__arsenal:hover & {
    width: 2rem;
  }

  &:hover {
    background: linear-gradient(180deg,
        color-mix(in srgb, var(--ci-primary) 22%, var(--ci-bg)),
        color-mix(in srgb, var(--ci-primary) 10%, var(--ci-bg)));
  }
}

.cs__slots-more-text {
  font-family: 'Inter', sans-serif;
  font-size: 0.6rem;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.06em;
  color: color-mix(in srgb, var(--ci-primary) 70%, var(--ci-dark));
  white-space: nowrap;
  writing-mode: vertical-rl;
  transform: rotate(180deg);
}

/* ── RPG Inventory Slot ── */
.wslot {
  --slot-rarity: var(--ci-primary);
  flex-shrink: 0;
  width: 76px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.3rem;
  padding: 0.4rem;
  border-radius: 6px;
  background: color-mix(in srgb, var(--ci-light) 6%, var(--ci-bg));
  border: 1px solid color-mix(in srgb, var(--ci-border) 40%, transparent);
  cursor: pointer;
  box-sizing: border-box;
  transition: transform 0.2s ease, box-shadow 0.2s ease, border-color 0.2s ease;

  &:hover {
    border-color: color-mix(in srgb, var(--slot-rarity) 50%, transparent);
    box-shadow:
      0 0 8px color-mix(in srgb, var(--slot-rarity) 20%, transparent),
      0 4px 12px color-mix(in srgb, var(--ci-dark) 12%, transparent);
  }

  /* ── Tier rarity colors ── */
  &--common   { --slot-rarity: #8a8a9a; }
  &--uncommon { --slot-rarity: #5dbe5d; }
  &--rare     { --slot-rarity: #4a8ad4; }
  &--epic     { --slot-rarity: #9a5acd; }
  &--legendary { --slot-rarity: #e8a830; }

  &--empty {
    opacity: 0.35;
    border-style: dashed;
    border-color: color-mix(in srgb, var(--ci-muted) 25%, transparent);

    &:hover {
      opacity: 0.55;
      border-color: color-mix(in srgb, var(--ci-muted) 40%, transparent);
      box-shadow: 0 2px 6px color-mix(in srgb, var(--ci-dark) 6%, transparent);
    }
  }

  @media (max-width: $bp-compact) {
    width: 68px;
  }

  @media (max-width: $bp-mobile) {
    width: 62px;
  }
}

.wslot__frame {
  flex-shrink: 0;
  width: 52px;
  height: 52px;
  border-radius: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  background:
    linear-gradient(145deg,
      color-mix(in srgb, var(--slot-rarity) 10%, var(--ci-bg)),
      color-mix(in srgb, var(--ci-dark) 8%, var(--ci-bg)));
  border: 1.5px solid color-mix(in srgb, var(--slot-rarity) 30%, transparent);
  box-shadow:
    inset 0 1px 0 color-mix(in srgb, var(--ci-light) 20%, transparent),
    inset 0 -1px 2px color-mix(in srgb, var(--ci-dark) 8%, transparent),
    0 0 0 1px color-mix(in srgb, var(--slot-rarity) 6%, transparent);

  /* Corner accents */
  &::before,
  &::after {
    content: '';
    position: absolute;
    width: 6px;
    height: 6px;
    border-color: color-mix(in srgb, var(--slot-rarity) 40%, transparent);
    border-style: solid;
    pointer-events: none;
    z-index: 2;
    transition: border-color 0.2s ease;
  }

  &::before {
    top: -1px;
    left: -1px;
    border-width: 1.5px 0 0 1.5px;
    border-radius: 2px 0 0 0;
  }

  &::after {
    bottom: -1px;
    right: -1px;
    border-width: 0 1.5px 1.5px 0;
    border-radius: 0 0 2px 0;
  }

  .wslot:hover & {
    border-color: color-mix(in srgb, var(--slot-rarity) 55%, transparent);
    box-shadow:
      inset 0 0 10px color-mix(in srgb, var(--slot-rarity) 12%, transparent),
      inset 0 1px 0 color-mix(in srgb, var(--ci-light) 20%, transparent),
      0 0 6px color-mix(in srgb, var(--slot-rarity) 18%, transparent);

    &::before,
    &::after {
      border-color: color-mix(in srgb, var(--slot-rarity) 65%, transparent);
    }
  }

  .wslot--empty & {
    &::before,
    &::after {
      display: none;
    }
  }

  @media (max-width: $bp-compact) {
    width: 46px;
    height: 46px;
  }

  @media (max-width: $bp-mobile) {
    width: 40px;
    height: 40px;
  }
}

.wslot__icon {
  font-size: 1.2rem;
  position: relative;
  z-index: 1;
  filter: drop-shadow(0 1px 2px color-mix(in srgb, var(--ci-dark) 15%, transparent));
}

.wslot__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 3px;
  position: relative;
  z-index: 1;
}

.wslot__qty {
  position: absolute;
  bottom: -3px;
  right: -3px;
  z-index: 3;
  font-family: 'DM Mono', monospace;
  font-size: 0.6rem;
  font-weight: 600;
  line-height: 1;
  min-width: 16px;
  height: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 3px;
  border-radius: 3px;
  background: color-mix(in srgb, var(--ci-dark) 85%, var(--ci-bg));
  color: var(--ci-light);
  border: 1px solid color-mix(in srgb, var(--slot-rarity) 40%, transparent);
  box-shadow: 0 1px 3px color-mix(in srgb, var(--ci-dark) 40%, transparent);
}

.wslot__tier {
  position: absolute;
  top: -3px;
  left: -3px;
  z-index: 3;
  font-family: 'Inter', sans-serif;
  font-size: 0.42rem;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.04em;
  line-height: 1;
  padding: 2px 3px;
  border-radius: 2px;
  background: var(--slot-rarity);
  color: #fff;
  box-shadow: 0 1px 3px color-mix(in srgb, var(--ci-dark) 40%, transparent);
  text-shadow: 0 0.5px 1px rgba(0, 0, 0, 0.3);
}

.wslot__name {
  font-family: 'Inter', sans-serif;
  font-size: 0.55rem;
  font-weight: 600;
  color: var(--ci-fg);
  text-align: center;
  line-height: 1.15;
  max-width: 72px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;

  .wslot--empty & {
    color: var(--ci-muted);
    font-weight: 500;
    text-transform: uppercase;
    letter-spacing: 0.05em;
    font-size: 0.5rem;
  }

  @media (max-width: $bp-mobile) {
    font-size: 0.48rem;
  }
}

/* ══════════════════════════════════
   DEITY CARD + POWER CARDS
   ══════════════════════════════════ */
@keyframes dcard-float {

  0%,
  100% {
    transform: translateY(0);
  }

  50% {
    transform: translateY(-4px);
  }
}

@keyframes dcard-ring {

  0%,
  100% {
    opacity: 0.15;
    transform: scale(1);
  }

  50% {
    opacity: 0.3;
    transform: scale(1.06);
  }
}

.cs__powers-row {
  display: flex;
  gap: 0.6rem;
  margin-bottom: 1rem;
  min-height: 383.1px;

  @media (max-width: $bp-medium) {
    min-height: auto;
  }

  @media (max-width: $bp-compact) {
    flex-direction: column;
    min-height: auto;
  }
}

/* ── Deity card (left half) ── */
.dcard {
  flex-shrink: 0;
  width: 140px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  padding: 1.2rem 0.8rem;
  border-radius: 16px;
  background:
    radial-gradient(ellipse at 50% 30%,
      color-mix(in srgb, var(--ci-primary) 12%, var(--ci-bg-alt)),
      color-mix(in srgb, var(--ci-primary) 3%, var(--ci-bg-alt)) 70%);
  border: 1px solid color-mix(in srgb, var(--ci-primary) 15%, transparent);
  position: relative;
  overflow: hidden;
  transition: box-shadow 0.3s ease, border-color 0.3s ease;

  /* Decorative corner glints */
  &::before,
  &::after {
    content: '';
    position: absolute;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background: radial-gradient(circle, color-mix(in srgb, var(--ci-primary) 8%, transparent), transparent 70%);
    animation: dcard-ring 4s ease-in-out infinite;
  }

  &::before {
    top: -10px;
    left: -10px;
  }

  &::after {
    bottom: -10px;
    right: -10px;
    animation-delay: 2s;
  }

  @media (min-width: $bp-expanded-wide) {
    width: 160px;
  }

  @media (max-width: $bp-medium) {
    width: 100px;
    padding: 0.6rem 0.2rem;
    gap: 0.8rem;
  }

  @media (max-width: $bp-compact) {
    width: 100%;
    padding: 1rem 0.2rem;
    gap: 0.8rem;
  }

  @media (max-width: $bp-mobile) {
    padding: 1rem 0.7rem;
    gap: 0.6rem;
  }
}

.dcard__icons {
  display: flex;
  align-items: flex-start;
  justify-content: center;
  gap: 0.4rem;

  &--dual {
    flex-direction: column;
    gap: 1rem;

    @media (max-width: $bp-compact) {
      flex-direction: row;
    }

    .dcard__icon {
      @media (max-width: $bp-medium) {
        width: 44px;
        height: 44px;

        svg {
          width: 24px;
          height: 24px;
        }
      }
    }
  }
}

.dcard__deity {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 0.35rem;
  width: 100%;
}

.dcard__icon {
  width: 72px;
  height: 72px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  animation: dcard-float 5s ease-in-out infinite;
  background:
    radial-gradient(circle at 40% 35%,
      color-mix(in srgb, var(--ci-primary) 16%, var(--ci-bg)),
      color-mix(in srgb, var(--ci-primary) 5%, var(--ci-bg)));
  border: 1.5px solid color-mix(in srgb, var(--ci-primary) 20%, transparent);
  box-shadow:
    0 0 0 4px color-mix(in srgb, var(--ci-primary) 6%, transparent),
    0 0 20px color-mix(in srgb, var(--ci-primary) 10%, transparent),
    inset 0 1px 0 color-mix(in srgb, var(--ci-light) 25%, transparent);

  svg {
    width: 40px;
    height: 40px;
    color: color-mix(in srgb, var(--ci-primary) 80%, var(--ci-dark));
    filter: drop-shadow(0 0 6px color-mix(in srgb, var(--ci-primary) 20%, transparent));
  }

  @media (min-width: $bp-expanded-wide) {
    width: 78px;
    height: 78px;

    svg {
      width: 44px;
      height: 44px;
    }
  }

  @media (max-width: $bp-medium) {
    width: 52px;
    height: 52px;

    svg {
      width: 30px;
      height: 30px;
    }
  }

  @media (max-width: $bp-compact) {
    width: 46px;
    height: 46px;

    svg {
      width: 26px;
      height: 26px;
    }
  }

  @media (max-width: $bp-mobile) {
    width: 40px;
    height: 40px;

    svg {
      width: 22px;
      height: 22px;
    }
  }
}

.dcard__fallback {
  font-size: 2rem;
}

.dcard__line {
  width: 40px;
  height: 1px;
  background: linear-gradient(90deg, transparent, color-mix(in srgb, var(--ci-primary) 35%, transparent), transparent);
}

.dcard__label {
  font-family: 'Playfair Display', serif;
  font-size: 0.7rem;
  font-weight: 700;
  color: var(--ci-fg);
  text-align: center;
  line-height: 1.2;
  text-transform: capitalize;

  @media (max-width: $bp-mobile) {
    font-size: 0.6rem;
  }
}

.dcard__sub {
  font-family: 'Inter', sans-serif;
  font-size: 0.5rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.16em;
  color: var(--ci-muted);

  @media (max-width: $bp-mobile) {
    font-size: 0.42rem;
  }
}

/* ── Power list (right half, single column) ── */
.cs__powers {
  flex: 1;
  min-width: 0;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  gap: 0.5rem;
}

.cs__powers-loader {
  display: flex;
  align-items: center;
  justify-content: center;
  flex: 1;
}

.cs__ptext {
  font-family: 'Kalam', 'Noto Sans Thai', cursive;
  font-size: 0.95rem;
  line-height: 1.75;
  color: var(--ci-fg);
  margin: 0;
  white-space: pre-wrap;

  &--empty {
    color: var(--ci-muted);
    text-align: center;
  }
}

@keyframes pcard-enter {
  from {
    opacity: 0;
    transform: translateY(8px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes pcard-ult-shimmer {
  0% {
    background-position: -200% center;
  }

  100% {
    background-position: 200% center;
  }
}

@keyframes pcard-ult-glow {

  0%,
  100% {
    box-shadow:
      0 0 0 1px color-mix(in srgb, var(--ci-accent) 20%, transparent),
      0 2px 12px color-mix(in srgb, var(--ci-primary) 8%, transparent);
  }

  50% {
    box-shadow:
      0 0 0 1px color-mix(in srgb, var(--ci-accent) 35%, transparent),
      0 0 24px color-mix(in srgb, var(--ci-primary) 18%, transparent),
      0 0 48px color-mix(in srgb, var(--ci-accent) 8%, transparent);
  }
}

/* ── Card base ── */
.pcard {
  --pc-color: var(--ci-primary);
  display: flex;
  border-radius: 14px;
  overflow: hidden;
  background:
    linear-gradient(160deg,
      color-mix(in srgb, var(--pc-color) 6%, var(--ci-bg-alt)),
      color-mix(in srgb, var(--pc-color) 2%, var(--ci-bg-alt)) 70%);
  border: 1px solid color-mix(in srgb, var(--pc-color) 12%, transparent);
  transition: transform 0.25s ease, box-shadow 0.3s ease;
  animation: pcard-enter 0.4s ease both;

  /* ── Passive modifier ── */
  &--passive {
    --pc-color: color-mix(in srgb, var(--ci-primary) 70%, var(--ci-accent-soft));
  }

  /* ── Skill modifier ── */
  &--skill {
    --pc-color: var(--ci-primary);
  }

  /* ── Ultimate modifier ── */
  &--ult {
    --pc-color: var(--ci-accent);
    border-color: color-mix(in srgb, var(--ci-accent) 25%, transparent);
    animation: pcard-enter 0.4s ease both, pcard-ult-glow 3s ease-in-out 0.5s infinite;

    background:
      linear-gradient(135deg,
        color-mix(in srgb, var(--ci-primary) 10%, var(--ci-bg-alt)),
        color-mix(in srgb, var(--ci-accent-soft) 12%, var(--ci-bg-alt)) 40%,
        color-mix(in srgb, var(--ci-primary) 6%, var(--ci-bg-alt)));
  }

  @media (max-width: $bp-medium) {
    border-radius: 12px;
  }

  @media (max-width: $bp-mobile) {
    border-radius: 10px;
  }
}

/* ── Left accent strip ── */
.pcard__accent {
  flex-shrink: 0;
  width: 4px;
  margin-right: 12px;
  background: linear-gradient(180deg,
      var(--pc-color),
      color-mix(in srgb, var(--pc-color) 40%, transparent));

  .pcard--ult & {
    width: 5px;
    background: linear-gradient(180deg,
        var(--ci-primary),
        var(--ci-accent),
        var(--ci-primary));
  }
}

/* ── Card body ── */
.pcard__body {
  flex: 1;
  padding: 0.65rem 0.8rem;
  min-width: 0;

  @media (max-width: $bp-medium) {
    padding: 0.5rem 0.7rem;
  }

  @media (max-width: $bp-compact) {
    padding: 0.45rem 0.6rem;
  }

  @media (max-width: $bp-mobile) {
    padding: 0.4rem 0.5rem;
  }
}

/* ── Icon orb ── */
.pcard__orb {
  flex-shrink: 0;
  align-self: center;
  height: calc(100% - 0.65rem);
  max-height: 40px;
  aspect-ratio: 1 / 1;
  margin: 0.65rem 0.8rem;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  background:
    linear-gradient(145deg,
      color-mix(in srgb, var(--pc-color) 14%, var(--ci-bg)),
      color-mix(in srgb, var(--pc-color) 4%, var(--ci-bg)) 50%,
      color-mix(in srgb, var(--pc-color) 10%, var(--ci-bg)));
  border: 1.5px solid color-mix(in srgb, var(--pc-color) 18%, transparent);
  box-shadow:
    inset 0 1px 0 color-mix(in srgb, var(--ci-light) 35%, transparent),
    inset 0 -1px 0 color-mix(in srgb, var(--pc-color) 6%, transparent),
    0 0 0 1px color-mix(in srgb, var(--pc-color) 4%, transparent),
    0 2px 8px color-mix(in srgb, var(--pc-color) 10%, transparent),
    0 0 16px color-mix(in srgb, var(--pc-color) 4%, transparent);
  transition: box-shadow 0.3s ease, transform 0.3s ease;

  /* Shine highlight */
  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 50%;
    border-radius: 8.5px 8.5px 0 0;
    background: linear-gradient(180deg,
      color-mix(in srgb, var(--ci-light) 22%, transparent),
      transparent);
    pointer-events: none;
  }

  /* Outer ring glow */
  &::after {
    content: '';
    position: absolute;
    inset: -3px;
    border-radius: 13px;
    border: 1px solid color-mix(in srgb, var(--pc-color) 8%, transparent);
    pointer-events: none;
    transition: border-color 0.3s ease, box-shadow 0.3s ease;
  }

  .pcard:hover & {
    box-shadow:
      inset 0 1px 0 color-mix(in srgb, var(--ci-light) 40%, transparent),
      inset 0 -1px 0 color-mix(in srgb, var(--pc-color) 8%, transparent),
      0 0 0 1px color-mix(in srgb, var(--pc-color) 12%, transparent),
      0 4px 14px color-mix(in srgb, var(--pc-color) 18%, transparent),
      0 0 24px color-mix(in srgb, var(--pc-color) 10%, transparent);

    &::after {
      border-color: color-mix(in srgb, var(--pc-color) 20%, transparent);
      box-shadow: 0 0 8px color-mix(in srgb, var(--pc-color) 12%, transparent);
    }
  }

  .pcard--ult & {
    background:
      linear-gradient(145deg,
        color-mix(in srgb, var(--ci-primary) 14%, var(--ci-bg)),
        color-mix(in srgb, var(--ci-accent) 6%, var(--ci-bg)) 50%,
        color-mix(in srgb, var(--ci-primary) 10%, var(--ci-bg)));
    border-color: color-mix(in srgb, var(--ci-accent) 25%, transparent);
    box-shadow:
      inset 0 1px 0 color-mix(in srgb, var(--ci-light) 35%, transparent),
      inset 0 -1px 0 color-mix(in srgb, var(--ci-accent) 6%, transparent),
      0 0 0 1px color-mix(in srgb, var(--ci-accent) 6%, transparent),
      0 2px 10px color-mix(in srgb, var(--ci-accent) 12%, transparent),
      0 0 20px color-mix(in srgb, var(--ci-accent) 6%, transparent);

    &::after {
      border-color: color-mix(in srgb, var(--ci-accent) 12%, transparent);
    }
  }

  @media (max-width: $bp-medium) {
    margin: 0.45rem 0.5rem;
    border-radius: 8px;

    &::before { border-radius: 6.5px 6.5px 0 0; }
    &::after { inset: -3px; border-radius: 11px; }
  }

  @media (max-width: $bp-compact) {
    margin: 0.4rem 0.45rem;
    height: 46px;
    max-height: none;
    border-radius: 7px;

    &::before { border-radius: 5.5px 5.5px 0 0; }
    &::after { inset: -2px; border-radius: 9px; }
  }

  @media (max-width: $bp-mobile) {
    margin: 0.35rem 0.4rem;
    height: 42px;
    max-height: none;
    border-radius: 6px;

    &::before { border-radius: 4.5px 4.5px 0 0; }
    &::after { inset: -2px; border-radius: 8px; }
  }
}

.pcard__orb-icon {
  font-size: 0.85rem;
  line-height: 1;
  color: var(--pc-color);
  position: relative;
  z-index: 1;
  filter: drop-shadow(0 0 3px color-mix(in srgb, var(--pc-color) 20%, transparent));
  transition: filter 0.3s ease;

  .pcard:hover & {
    filter: drop-shadow(0 0 6px color-mix(in srgb, var(--pc-color) 35%, transparent));
  }

  .pcard--ult & {
    background: linear-gradient(135deg, var(--ci-primary), var(--ci-accent));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    filter: drop-shadow(0 0 4px color-mix(in srgb, var(--ci-accent) 25%, transparent));
  }

  @media (max-width: $bp-compact) {
    font-size: 1rem;
  }

  @media (max-width: $bp-mobile) {
    font-size: 0.95rem;
  }
}

.pcard__tag {
  display: inline-block;
  font-family: 'Inter', sans-serif;
  font-size: 0.5rem;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.14em;
  color: var(--ci-tag-color, color-mix(in srgb, var(--pc-color) 80%, var(--ci-dark)));
  background: color-mix(in srgb, var(--ci-highlight) 30%, transparent);
  padding: 0.1rem 0.45rem;
  border-radius: 3px;
  margin-bottom: 0.15rem;

  .pcard--ult & {
    background:
      linear-gradient(90deg,
        color-mix(in srgb, var(--ci-primary) 14%, var(--ci-highlight)),
        color-mix(in srgb, var(--ci-accent-soft) 20%, var(--ci-highlight)));
    background-size: 200% 100%;
    animation: pcard-ult-shimmer 4s ease-in-out infinite;
  }

  @media (max-width: $bp-compact) {
    font-size: 0.45rem;
  }

  @media (max-width: $bp-mobile) {
    font-size: 0.4rem;
    letter-spacing: 0.1em;
  }
}

.pcard__name {
  font-size: 0.9rem;
  font-weight: 700;
  color: var(--ci-fg);
  margin: 0;
  line-height: 1.25;

  @media (max-width: $bp-medium) {
    font-size: 0.82rem;
  }

  @media (max-width: $bp-compact) {
    font-size: 0.78rem;
  }

  @media (max-width: $bp-mobile) {
    font-size: 0.72rem;
  }
}

.pcard__desc {
  font-family: 'Noto Sans Thai', 'Inter', sans-serif;
  font-size: 0.75rem;
  line-height: 1.65;
  color: color-mix(in srgb, var(--ci-fg) 70%, transparent);
  margin: 0.25rem 0 0;

  .pcard--ult & {
    color: color-mix(in srgb, var(--ci-fg) 80%, transparent);
  }

  @media (max-width: $bp-medium) {
    font-size: 0.7rem;
    line-height: 1.55;
  }

  @media (max-width: $bp-compact) {
    font-size: 0.65rem;
    line-height: 1.5;
  }

  @media (max-width: $bp-mobile) {
    font-size: 0.6rem;
    line-height: 1.45;
  }
}

/* ══════════════════════════════════
   PERSONAL INFO — wrapper card
   ══════════════════════════════════ */
.cs__personal {
  margin-bottom: 0.8rem;
  border-radius: 12px;
  background:
    linear-gradient(160deg,
      color-mix(in srgb, var(--ci-primary) 4%, var(--ci-bg-alt)),
      color-mix(in srgb, var(--ci-accent-soft) 4%, var(--ci-bg-alt)) 70%);
  border: 1px solid color-mix(in srgb, var(--ci-primary) 10%, transparent);
  overflow: hidden;
  transition: box-shadow 0.25s ease;
}

.cs__personal-title {
  display: flex;
  align-items: center;
  gap: 0.45rem;
  padding: 0.55rem 0.7rem;
  border-bottom: 1px solid color-mix(in srgb, var(--ci-primary) 10%, transparent);
  background:
    linear-gradient(90deg,
      color-mix(in srgb, var(--ci-primary) 14%, var(--ci-bg-alt)),
      color-mix(in srgb, var(--ci-accent-soft) 8%, var(--ci-bg-alt)));
  font-size: 0.8rem;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.12em;
  color: color-mix(in srgb, var(--ci-primary-hover) 85%, var(--ci-dark));
  margin: 0;
}

.cs__personal-diamond {
  font-size: 0.45rem;
  color: var(--ci-primary);
  animation: cs-pulse-diamond 2.5s ease-in-out infinite;
  animation-delay: 1s;
}

.cs__personal-stats {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 0.4rem;
  padding: 0.5rem;
  border-bottom: 1px solid color-mix(in srgb, var(--ci-primary) 8%, transparent);

  @media (max-width: $bp-medium) {
    grid-template-columns: repeat(4, 1fr);
  }

  @media (max-width: $bp-compact) {
    grid-template-columns: repeat(2, 1fr);
  }
}

.cs__personal-stat {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.15rem;
  padding: 0.6rem 0.3rem 0.5rem;
  border-radius: 10px;
  background:
    radial-gradient(ellipse at 50% 20%,
      color-mix(in srgb, var(--ci-primary) 6%, transparent),
      transparent 70%);
  border: 1px solid color-mix(in srgb, var(--ci-primary) 6%, transparent);
  transition: border-color 0.2s, box-shadow 0.2s, background 0.2s;
}

.cs__personal-stat-icon {
  width: 1.3rem;
  height: 1.3rem;
  color: color-mix(in srgb, var(--ci-primary) 50%, var(--ci-muted));
  flex-shrink: 0;
  margin-bottom: 0.15rem;
  transition: color 0.2s, filter 0.2s;

  @media (max-width: $bp-mobile) {
    width: 1.1rem;
    height: 1.1rem;
  }
}

.cs__personal-stat-label {
  font-family: 'Inter', sans-serif;
  font-size: 0.5rem;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  color: var(--ci-muted);

  @media (max-width: $bp-mobile) {
    font-size: 0.42rem;
  }
}

.cs__personal-stat-value {
  font-family: 'DM Mono', monospace;
  font-size: 0.72rem;
  font-weight: 500;
  color: color-mix(in srgb, var(--ci-fg) 85%, transparent);
  text-align: center;
  line-height: 1.3;

  @media (max-width: $bp-mobile) {
    font-size: 0.62rem;
  }
}

/* ── Identity row — swatches + info side by side ── */
.cs__identity-row {
  display: flex;
  flex-wrap: wrap;
  gap: 0;
  align-items: stretch;
  border-bottom: 1px solid color-mix(in srgb, var(--ci-primary) 8%, transparent);

  @media (max-width: $bp-medium) {
    flex-direction: column;

    .cs__scrapbook {
      flex-direction: row;
      border-right: none;
      justify-content: center;
    }
  }
}

/* ── Scrapbook — color swatches ── */
.cs__scrapbook {
  flex-shrink: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.6rem;
  padding: 0.6rem 0.7rem;
  border-right: 1px solid color-mix(in srgb, var(--ci-primary) 8%, transparent);
}

.cs__swatch {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.25rem;
  padding: 0.6rem 1rem 0.5rem;
  min-width: 90px;
  border-radius: 4px;
  position: relative;
  background:
    linear-gradient(170deg,
      color-mix(in srgb, var(--ci-primary) 5%, var(--ci-light)),
      color-mix(in srgb, var(--ci-accent) 3%, var(--ci-light)) 70%);
  border: 1px solid color-mix(in srgb, var(--ci-primary) 10%, transparent);
  box-shadow:
    1px 2px 5px color-mix(in srgb, var(--ci-dark) 8%, transparent),
    inset 0 0 0 0.5px color-mix(in srgb, var(--ci-light) 40%, transparent);
  transition: transform 0.3s ease, box-shadow 0.3s ease;

  /* Tape strip */
  &::before {
    content: '';
    position: absolute;
    top: -5px;
    left: 50%;
    transform: translateX(-50%) rotate(-2deg);
    width: 28px;
    height: 9px;
    background: color-mix(in srgb, var(--ci-accent) 22%, var(--ci-light));
    border-radius: 1px;
    opacity: 0.75;
    box-shadow: 0 1px 2px color-mix(in srgb, var(--ci-dark) 5%, transparent);
  }

  &:first-child {
    transform: rotate(-1.5deg);
  }

  &:last-child {
    transform: rotate(1.2deg);

    &::before {
      transform: translateX(-50%) rotate(3deg);
    }
  }

  &:hover {
    transform: rotate(0deg);
    z-index: 1;
  }

  @media (max-width: $bp-medium) {
    min-width: 80px;
    padding: 0.5rem 0.8rem 0.4rem;
  }

  @media (max-width: $bp-compact) {
    min-width: 75px;
    padding: 0.45rem 0.7rem 0.35rem;

    &:first-child {
      transform: rotate(-0.8deg);
    }

    &:last-child {
      transform: rotate(0.6deg);
    }
  }

  @media (max-width: $bp-mobile) {
    min-width: 68px;
    padding: 0.4rem 0.6rem 0.3rem;

    &,
    &:first-child,
    &:last-child {
      transform: none;
    }

    &::before {
      display: none;
    }
  }
}

.cs__swatch-circle {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  border: 2px solid color-mix(in srgb, var(--ci-primary) 15%, transparent);
  box-shadow:
    0 0 0 3px color-mix(in srgb, var(--ci-light) 60%, transparent),
    0 2px 6px color-mix(in srgb, var(--ci-dark) 12%, transparent),
    inset 0 2px 4px rgba(255, 255, 255, 0.3);
  transition: transform 0.2s, box-shadow 0.2s;

  .cs__swatch:hover & {
    box-shadow:
      0 0 0 3px color-mix(in srgb, var(--ci-light) 60%, transparent),
      0 0 12px color-mix(in srgb, var(--ci-primary) 20%, transparent),
      inset 0 2px 4px rgba(255, 255, 255, 0.3);
  }

  @media (max-width: $bp-compact) {
    width: 26px;
    height: 26px;
  }

  @media (max-width: $bp-mobile) {
    width: 22px;
    height: 22px;
  }
}

.cs__swatch-label {
  font-family: 'Inter', sans-serif;
  font-size: 0.48rem;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  color: var(--ci-muted);

  @media (max-width: $bp-mobile) {
    font-size: 0.4rem;
  }
}

.cs__swatch-hex {
  font-family: 'DM Mono', monospace;
  font-size: 0.65rem;
  font-weight: 500;
  color: color-mix(in srgb, var(--ci-fg) 60%, transparent);

  @media (max-width: $bp-mobile) {
    font-size: 0.55rem;
  }
}

/* ── Appearance text ── */
.cs__appearance {
  flex: 1;
  min-width: 0;
  min-height: 100%;
  display: flex;
  flex-direction: column;
  gap: 0.4rem;
  padding: 0.7rem 0.9rem;
}

.cs__appearance-label {
  font-size: 1rem;
  font-weight: 700;
  color: color-mix(in srgb, var(--ci-primary) 75%, var(--ci-dark));
  padding-bottom: 0.4rem;
  margin-bottom: 0.4rem;
  border-bottom: 2px solid color-mix(in srgb, var(--ci-primary) 15%, transparent);

  @media (max-width: $bp-compact) {
    font-size: 0.85rem;
  }

  @media (max-width: $bp-mobile) {
    font-size: 0.8rem;
  }
}

.cs__appearance-text {
  font-family: 'Kalam', 'Noto Sans Thai', cursive;
  font-size: 0.88rem;
  line-height: 1.75;
  color: color-mix(in srgb, var(--ci-fg) 80%, transparent);
  margin: 0;
  white-space: pre-wrap;
  padding-left: 0.5rem;
  border-left: 3px solid var(--ci-primary);
  border-image: linear-gradient(180deg, var(--ci-primary), color-mix(in srgb, var(--ci-accent) 40%, transparent)) 1;
  max-height: 150px;
  overflow-y: auto;
  scrollbar-width: thin;
  scrollbar-color: color-mix(in srgb, var(--ci-primary) 15%, transparent) transparent;

  &::-webkit-scrollbar {
    width: 4px;
  }

  &::-webkit-scrollbar-track {
    background: transparent;
  }

  &::-webkit-scrollbar-thumb {
    background: color-mix(in srgb, var(--ci-primary) 15%, transparent);
    border-radius: 2px;
  }

  @media (max-width: $bp-medium) {
    max-height: unset;
    font-size: 0.82rem;
  }

  @media (max-width: $bp-compact) {
    font-size: 0.8rem;
  }

  @media (max-width: $bp-mobile) {
    font-size: 0.75rem;
  }
}

.cs__human-parent {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  width: 100%;
  padding: 0.5rem 0.7rem;
  border-top: 1px dashed color-mix(in srgb, var(--ci-primary) 12%, transparent);

  @media (max-width: $bp-medium) {
    gap: 0.5rem;
  }
}

.cs__human-parent-entry {
  display: flex;
  align-items: center;
  gap: 0.35rem;
}

.cs__human-parent-icon {
  width: 1.1rem;
  height: 1.1rem;
  flex-shrink: 0;
  color: color-mix(in srgb, var(--ci-primary) 50%, var(--ci-muted));

  @media (max-width: $bp-mobile) {
    width: 0.9rem;
    height: 0.9rem;
  }
}

.cs__human-parent-label {
  font-family: 'Inter', sans-serif;
  font-size: 0.5rem;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.08em;
  color: var(--ci-muted);
  padding-top: 3px;

  @media (max-width: $bp-mobile) {
    font-size: 0.42rem;
  }
}

.cs__human-parent-name {
  font-family: 'Kalam', 'Noto Sans Thai', cursive;
  font-size: 0.85rem;
  color: color-mix(in srgb, var(--ci-fg) 85%, transparent);
  padding-top: 3px;
  padding-left: 3px;

  @media (max-width: $bp-mobile) {
    font-size: 0.75rem;
  }
}

.cs__human-parent-entry--divine {
  .cs__human-parent-icon {
    color: var(--ci-accent);
  }

  .cs__human-parent-label {
    color: color-mix(in srgb, var(--ci-accent) 70%, var(--ci-muted));
  }

  .cs__human-parent-name {
    color: var(--ci-primary);
    font-weight: 600;
  }
}

/* ── Two-column masonry ── */
/* ── Trait chip boxes ── */
.cs__trait-row {
  display: flex;
  gap: 0.5rem;

  >.cs__trait {
    flex: 1;
    min-width: 0;
  }

  @media (max-width: $bp-compact) {
    flex-direction: column;
  }
}

.cs__trait {
  border-radius: 12px;
  background:
    linear-gradient(160deg,
      color-mix(in srgb, var(--ci-primary) 6%, var(--ci-bg-alt)),
      color-mix(in srgb, var(--ci-primary) 2%, var(--ci-bg-alt)) 70%);
  border: 1px solid color-mix(in srgb, var(--ci-primary) 12%, transparent);
  overflow: hidden;
  margin-bottom: 0.5rem;
  transition: box-shadow 0.25s ease;
}

.cs__trait-label {
  display: flex;
  align-items: center;
  gap: 0.45rem;
  padding: 0.55rem 0.7rem;
  border-bottom: 1px solid color-mix(in srgb, var(--ci-primary) 12%, transparent);
  background:
    linear-gradient(90deg,
      color-mix(in srgb, var(--ci-primary) 18%, var(--ci-bg-alt)),
      color-mix(in srgb, var(--ci-primary) 8%, var(--ci-bg-alt)));
  font-family: 'Inter', 'Noto Sans Thai', sans-serif;
  font-size: 0.8rem;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.12em;
  color: color-mix(in srgb, var(--ci-primary-hover) 90%, var(--ci-dark));
  margin: 0;

  @media (max-width: $bp-compact) {
    font-size: 0.7rem;
    padding: 0.45rem 0.6rem;
  }
}

.cs__trait-diamond {
  font-size: 0.45rem;
  color: var(--ci-primary);
  animation: cs-pulse-diamond 2.5s ease-in-out infinite;
  animation-delay: 1.5s;
}

.cs__trait-chips {
  display: flex;
  flex-wrap: wrap;
  gap: 0.35rem;
  padding: 0.55rem 0.65rem;
  min-height: 5rem;

  @media (max-width: $bp-compact) {
    min-height: 2.8rem;
    padding: 0.45rem 0.5rem;
    gap: 0.3rem;
  }

  @media (max-width: $bp-mobile) {
    min-height: 2.5rem;
  }
}

.cs__trait-empty {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.4rem;
  width: 100%;
  opacity: 0.45;
}

.cs__trait-empty-icon {
  width: 1.1rem;
  height: 1.1rem;
  color: var(--ci-muted);

  @media (max-width: $bp-compact) {
    width: 0.95rem;
    height: 0.95rem;
  }
}

.cs__trait-empty-text {
  font-family: 'Inter', 'Noto Sans Thai', sans-serif;
  font-size: 0.7rem;
  color: color-mix(in srgb, var(--ci-fg) 85%, transparent);

  @media (max-width: $bp-compact) {
    font-size: 0.62rem;
  }
}

.cs__chip {
  display: inline-flex;
  flex-direction: column;
  padding: 0.3rem 0.6rem;
  border-radius: 20px;
  background: color-mix(in srgb, var(--ci-primary) 8%, var(--ci-surface));
  border: 1px solid color-mix(in srgb, var(--ci-primary) 12%, transparent);
  transition: background 0.2s, border-color 0.2s, box-shadow 0.2s, transform 0.2s;

  &:hover {
    transform: translateY(-2px);
    background: color-mix(in srgb, var(--ci-primary) 14%, var(--ci-surface-hover));
    border-color: color-mix(in srgb, var(--ci-primary-hover) 25%, transparent);
    box-shadow:
      0 3px 10px color-mix(in srgb, var(--ci-shadow) 25%, transparent),
      0 0 16px color-mix(in srgb, var(--ci-primary) 5%, transparent);
  }

  &--empty {
    background: transparent;
    border-color: transparent;
    color: var(--ci-muted);
    font-style: italic;
    font-family: 'Inter', sans-serif;
    font-size: 0.75rem;
  }

  @media (max-width: $bp-compact) {
    padding: 0.25rem 0.5rem;
  }
}

.cs__chip-title {
  font-family: 'Inter', 'Noto Sans Thai', sans-serif;
  font-size: 0.7rem;
  font-weight: 600;
  color: color-mix(in srgb, var(--ci-fg) 90%, transparent);
  line-height: 1.3;

  @media (max-width: $bp-compact) {
    font-size: 0.62rem;
  }

  @media (max-width: $bp-mobile) {
    font-size: 0.58rem;
  }
}

.cs__chip-desc {
  font-family: 'Kalam', 'Noto Sans Thai', cursive;
  font-size: 0.6rem;
  color: var(--ci-muted);
  line-height: 1.3;

  @media (max-width: $bp-compact) {
    font-size: 0.55rem;
  }

  @media (max-width: $bp-mobile) {
    font-size: 0.5rem;
  }
}

/* Accent variant (Weaknesses) */
.cs__trait--accent {
  background:
    linear-gradient(160deg,
      color-mix(in srgb, var(--ci-accent-soft) 10%, var(--ci-bg-alt)),
      color-mix(in srgb, var(--ci-accent-soft) 4%, var(--ci-bg-alt)) 70%);
  border-color: color-mix(in srgb, var(--ci-accent) 12%, transparent);

  .cs__trait-label {
    border-bottom-color: color-mix(in srgb, var(--ci-accent) 12%, transparent);
    background:
      linear-gradient(90deg,
        color-mix(in srgb, var(--ci-accent-soft) 22%, var(--ci-bg-alt)),
        color-mix(in srgb, var(--ci-accent-soft) 10%, var(--ci-bg-alt)));
    color: color-mix(in srgb, var(--ci-accent) 80%, var(--ci-dark));
  }

  .cs__chip {
    background: color-mix(in srgb, var(--ci-accent-soft) 12%, var(--ci-surface));
    border-color: color-mix(in srgb, var(--ci-accent) 12%, transparent);

    &:hover {
      background: color-mix(in srgb, var(--ci-accent-soft) 18%, var(--ci-surface-hover));
      border-color: color-mix(in srgb, var(--ci-accent) 25%, transparent);
    }
  }
}

/* Mixed variant (Abilities) */
.cs__trait--mixed {
  background:
    linear-gradient(160deg,
      color-mix(in srgb, var(--ci-primary) 4%, var(--ci-bg-alt)),
      color-mix(in srgb, var(--ci-accent-soft) 5%, var(--ci-bg-alt)) 70%);

  .cs__trait-label {
    background:
      linear-gradient(90deg,
        color-mix(in srgb, var(--ci-primary) 14%, var(--ci-bg-alt)),
        color-mix(in srgb, var(--ci-accent-soft) 10%, var(--ci-bg-alt)));
  }
}

/* ── Back button — left side inside actions ── */
.cs__back {
  margin-right: auto;
}

/* ══════════════════════════════════
   ACTION BUTTONS — top bar
   ══════════════════════════════════ */
.cs__actions {
  position: absolute;
  top: 1.2rem;
  left: 1.2rem;
  right: 1.2rem;
  display: flex;
  gap: 0.25rem;
  z-index: 5;
  justify-content: flex-end;

  @media (max-width: $bp-expanded) {
    top: 1rem;
    left: 1rem;
    right: 1rem;
  }

  @media (max-width: 1000px) {
    max-width: 300px;
  }

  @media (max-width: $bp-medium) {
    max-width: 100%;
  }

  @media (max-width: $bp-compact) {
    top: 0.8rem;
    left: 0.5rem;
    right: 0.5rem;
  }
}

.cs__action-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 34px;
  height: 34px;
  border-radius: 50%;
  border: 1px solid color-mix(in srgb, var(--ci-primary) 20%, transparent);
  background:
    radial-gradient(circle at 40% 35%,
      color-mix(in srgb, var(--ci-primary) 10%, var(--ci-surface)),
      color-mix(in srgb, var(--ci-primary) 4%, var(--ci-surface)));
  color: color-mix(in srgb, var(--ci-primary) 75%, var(--ci-dark));
  cursor: pointer;
  text-decoration: none;
  transition: background 0.2s, border-color 0.2s, box-shadow 0.2s, transform 0.2s;

  svg {
    width: 15px;
    height: 15px;
  }

  &:hover {
    border-color: color-mix(in srgb, var(--ci-primary-hover) 35%, transparent);
    background: color-mix(in srgb, var(--ci-primary) 16%, var(--ci-surface-hover));
    color: var(--ci-primary-hover);
  }

  &:last-child:not(:first-child) {
    margin-left: auto;
    margin-right: 0;
  }

  @media (max-width: $bp-compact) {
    width: 30px;
    height: 30px;

    svg {
      width: 13px;
      height: 13px;
    }
  }
}

